import {controller, target, targets} from '@github/catalyst'
import {TemplateInstance} from '@github/template-parts'

@controller
export default class AffectedProductRowElement extends HTMLElement {
  @target destroyInput: HTMLInputElement
  @target ecosystemOtherInputElement: HTMLInputElement
  @target ecosystemSelectElement: HTMLSelectElement
  @target packageInputElement: HTMLInputElement
  @target unsupportedEcosystemMessageElement: HTMLElement
  @targets inputs: Array<HTMLInputElement | HTMLSelectElement>
  @target packageUrlElement: HTMLElement
  @target packageLinkFoundTemplate: HTMLTemplateElement
  @target packageLinkNotFoundTemplate: HTMLTemplateElement

  connectedCallback() {
    // When choosing `Other`, navigating elsewhere, then clicking the browser back
    // button, the browser remembers the value for the select, but it does not
    // change the hidden attribute and make the input type text shown.
    // The timeout is necessary because the prefilling of the value might not
    // occur until this enclosing function execution is over.
    setTimeout(() => {
      if (this.ecosystemOtherInputElement) {
        this.ecosystemOtherInputElement.hidden = this.ecosystemSelectElement.value !== 'other'
      }
    }, 0)
    setTimeout(() => {
      this.showUnsupportedEcosystemMessage()
    })
    setTimeout(() => {
      this.showPackageUrlElement()
    })
  }

  clear() {
    for (const element of this.inputs) {
      element.value = ''
    }
    this.ecosystem = this.initialEcosystem
  }

  delete() {
    this.dispatchEvent(new CustomEvent('affected-product-row-delete', {bubbles: true, detail: this}))
  }

  focusFirstInput() {
    this.ecosystemSelectElement.focus()
  }

  hide() {
    this.hidden = true
    for (const input of this.inputs) {
      // So the enclosing form doesn't become invalid due to hidden fields that are required
      input.required = false
    }
  }

  get ecosystem(): string {
    return this.ecosystemSelectElement.value
  }

  set ecosystem(ecosystemValue: string) {
    this.ecosystemSelectElement.value = ecosystemValue
    this.ecosystemOtherInputElement.hidden = ecosystemValue !== 'other'
  }

  get ecosystemOther(): string {
    return this.ecosystemOtherInputElement.value
  }

  set ecosystemOther(ecosystemValue: string) {
    this.ecosystemOtherInputElement.value = ecosystemValue
  }

  get initialEcosystem() {
    return this.getAttribute('data-initial-ecosystem') || ''
  }

  get package(): string {
    return this.packageInputElement.value
  }

  set package(packageValue: string) {
    this.packageInputElement.value = packageValue
  }

  markToDestroy() {
    this.destroyInput.value = 'true'
  }

  handleInputBlur(event: InputEvent) {
    const input = event.target as HTMLInputElement
    const erroredFormGroup = input.closest('dl.form-group.errored')
    if (erroredFormGroup) {
      erroredFormGroup.classList.remove('errored')
    }
  }

  showUnsupportedEcosystemMessage() {
    if (!this.unsupportedEcosystemMessageElement) return

    if (this.ecosystemSelectElement.value) {
      this.unsupportedEcosystemMessageElement.hidden = this.ecosystemSelectElement.value === 'pip'
      return
    }
    this.unsupportedEcosystemMessageElement.hidden = true
  }

  async showPackageUrlElement() {
    const selectedItem = this.ecosystemSelectElement
    const ecosystem = selectedItem.value
    const packageName = this.packageInputElement.value

    if (!ecosystem || !packageName) {
      this.packageUrlElement.replaceChildren()
      return
    }

    const url = `/advisories/package-url?ecosystem=${ecosystem}&package_name=${packageName}`

    const response = await fetch(url, {
      method: 'GET',
      headers: {
        Accept: 'application/json',
      },
    })
    const res = await response.json()

    const ecosystemLabel = selectedItem?.options[selectedItem?.selectedIndex]?.innerHTML

    if (response.ok && res['package_url']) {
      const urlFoundTemplate = new TemplateInstance(this.packageLinkFoundTemplate, {
        packageUrl: res['package_url'],
        ecosystemLabel,
      })
      this.packageUrlElement.replaceChildren(urlFoundTemplate)
    } else if (res['error'] === 'not_found') {
      const urlNotFoundTemplate = new TemplateInstance(this.packageLinkNotFoundTemplate, {
        ecosystemLabel,
      })
      this.packageUrlElement.replaceChildren(urlNotFoundTemplate)
    } else {
      this.packageUrlElement.replaceChildren()
    }
  }
}
